.button {
  display: block;
  width: 100%;
  padding: 10px 20px 12px;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  text-align: center;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  user-select: none;
  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.6;
  }
}
